<template>
  <div>

    <img src="../assets/404.png" class="pic"/>
    <div class="msg">哎呀，您要找的页面迷路了</div>
    <div class="btn_wrap" v-on:touchstart="changeShade" v-on:touchend="changeShade" @click="returnPreview">
      <input type="button" value="返回上页" class="return_btn"/>
      <div class="global_btn_shade" v-show="ifShade"></div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'notFound',
  data () {
    return {
      ifShade: false
    }
  },
  methods: {
    returnPreview () {
      this.$router.go(-1)
    },
    changeShade () {
      this.ifShade = !this.ifShade
    }
  }
}
</script>

<style scoped lang="less">
  .pic {
    display: block;
    margin: 0 auto;
    margin-top: 1.2*2rem;
    width: 2.8*2rem;
    height: 1.93*2rem;
  }

  .msg {
    font-size: .14*2rem;
    color: #272727;
    margin-top: .52*2rem;
    text-align: center;
  }

  .btn_wrap {
    width: .9*2rem;
    height: .3*2rem;
    padding: 0;
    margin: 0 auto;
    margin-top: .14*2rem;
    position: relative;

    .return_btn {
      border-radius: 4px;
      width: 100%;
      height: 100%;
      text-align: center;
      border: none;
      box-shadow: 0 4px 6px 0 rgba(45, 136, 219, 0.3);
      display: block;
      background-color: #3391E8;
      color: #fff;
      font-size: .14*2rem;
    }

    .global_btn_shade {
      position: absolute;
      top: 0;
      /* left: 50%;
            transform: translateX(-50%); */
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, .3);
      transition: all .3s;
    }
  }

</style>



